@import url(http://db.onlinewebfonts.com/t/15db490066f3892032387f14ef589970.svg#Kamerik105W00-Bold);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.LandingVectorBG-container {
  display: flex;
  flex-direction: row;
}

/* .LandingVectorBG-container-faded .LandingVectorBG--left-upper,
.LandingVectorBG-container-faded .LandingVectorBG--left-lower,
.LandingVectorBG-container-faded .LandingVectorBG--right-upper,
.LandingVectorBG-container-faded */
.LandingVectorBG-container-faded {
  -webkit-animation: faded 0.4s ease-in-out 0s;
          animation: faded 0.4s ease-in-out 0s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

/* @keyframes moveToRight {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(var(--moveX));
    opacity: 0;
  }
} */

@-webkit-keyframes faded {
  0% {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
  100% {
    -webkit-filter: blur(20px);
            filter: blur(20px);
  }
}

@keyframes faded {
  0% {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
  100% {
    -webkit-filter: blur(20px);
            filter: blur(20px);
  }
}

.LandingVectorBG--left-upper {
  background: #6c63fe;
  width: 65vw;
  height: 50vh;
}

.LandingVectorBG--left-lower {
  width: 65vw;
  height: 50vh;
  background: #6c63fe;
  border-bottom-right-radius: 40% 100%;
}

.LandingVectorBG--right-upper {
  background: #6c63fe;
  width: 35vw;
  height: 50vh;
}

.LandingVectorBG--right-upper-corner {
  background: #fff;
  width: 35vw;
  height: 50vh;
  border-top-left-radius: 40% 100%;
}

.logo {
  position: absolute;
  left: 1vw;
  height: 100%;
  color: white;
  border: 2px white solid;
  width: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
.nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 70vw;
  transform: translate(0, 2vh);
}

.nav-bar-button {
  display: block;
  border-radius: 4px;
  padding-top: 10px;
  outline: 0px;
  font-weight: 600;
  color: #fff;
  padding-bottom: 10px;
  text-align: center;
  border: 0px;
  cursor: pointer;
  width: 13vw;
  border-radius: 20px;
  transition: all 0.4s ease-in-out;
}
.nav-bar-button:hover {
  color: #6c63fe;
  background-color: white;
}

.content-card {
  position: absolute;
  top: 21vh;
  right: 1.5vw;
  border-radius: 4px;
  padding: 20px 0px;
  text-align: center;
  height: 60vh;
  width: 30vw;
  background-color: #fff;
}

.content-card--title {
  font-family: "Kamerik105W00-Bold";
  color: black;
  font-size: 7vh;
  padding: 0 0 0 30px;
  text-align: left;
}

.content-card--title-alone {
  margin-left: 1vw;
  color: #ff6584;
}

.content-card--content {
  font-size: 2.7vh;
  padding: 0px 20px 0 30px;
  line-height: 6vh;
  margin-top: 3vh;
  text-align: justify;
}

.button {
  display: block;
  border-radius: 4px;
  padding-top: 10px;
  outline: 0px;
  font-weight: 600;
  color: #fff;
  padding-bottom: 10px;
  text-align: center;
  border: 0px;
  cursor: pointer;
  width: 15vw;
  transform: translate(15vw, 2vh);
  border-radius: 20px;
  background-color: #6c63fe;
  transition: all 0.4s ease-in-out;
  box-shadow: rgba(163, 177, 198, 0.6) 9px 9px 16px,
    rgba(255, 255, 255, 0.5) -9px -9px 16px;
}

.button:hover {
  color: #6c63fe;
  background: white;
}

.content-card--button-group {
  margin-top: 5vh;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-evenly;
}

.content-card--button-group-button {
  display: block;
  border-radius: 4px;
  padding-top: 10px;
  outline: 0px;
  padding-bottom: 10px;
  text-align: center;
  border: 0px;
  cursor: pointer;
  width: 10vw;
  border-radius: 20px;
  background-color: #6c63fe;
  transition: all 0.4s ease-in-out;
  box-shadow: rgba(163, 177, 198, 0.6) 9px 9px 16px,
    rgba(255, 255, 255, 0.5) -9px -9px 16px;
}

.content-card--button-group-button:hover,
.content-card--button-group-button:hover a {
  color: #6c63fe;
  background: white;
}

.content-card--button-group-button a {
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  background: #6c63fe;
  transition: all 0.4s ease-in-out;
}

#container {
  width: 100vw;
  height: 100vh;
  background-color: #6c63fe;
}

